.tableWrapper {
    width: 95vw;
    max-width: 1000px;
    margin: 2rem auto;
    overflow-x: auto;
}

.table {
    border-collapse: collapse;
    font-size: 0.8rem;
    width: 100%;
    border-radius: 5px 5px 0 0;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    td, th { 
        text-align: left;
        padding: 0.5rem;
        border: 1px solid #f4f4f4;
      }

    th {
        background: $dark-secondary;
        color: white;
    }

    .actionsTd {
        display: flex;
        justify-content: space-around;
    }  

    button {
        padding: 0.4rem;
        display: inline-block;
        margin-left: auto;
        margin-right: auto;
        font-size: 0.8rem;
        border: none;
        color: white;
        background: $font-on-white;
        border-radius: 0.25rem;
    }


    .deleteButtonContainer {
        display: flex;
        justify-content: center;
        button {
            background: red;
            font-weight: bold;
        }
    }
}

@media screen and (min-width: 768px){
    .table {
        font-size: 1rem;
        button {
            font-size: 1rem;
        }
    }
    
}
